import React from 'react'
import { Route, RouteComponentProps, RouteProps } from 'react-router'

import Pacman from '../atoms/Pacman'

interface IProps extends Omit<RouteProps, 'render' | 'children'> {
  component: React.ComponentType<RouteComponentProps> | React.ComponentType
}

export const LazyRoute = ({ component, ...props }: IProps) => (
  <Route
    {...props}
    render={routeProps => (
      <React.Suspense fallback={<Pacman />}>
        {React.createElement<RouteComponentProps | {}>(component, routeProps)}
      </React.Suspense>
    )}
  />
)
