import { types } from 'mobx-state-tree'

import { RM } from '../../../helpers/ramda'
import { BaseModel } from '../../../models/BaseModel'
import { BusinessHoursModel } from '../../businesshours/models/BusinessHoursModel'
import { MenuModel } from '../../menu/models/MenuModel'

export const SettingsModel = BaseModel.named('Settings')
  .props({
    selectedBranchId: types.maybe(types.string),
    businessHours: types.optional(BusinessHoursModel, {}),
    menu: types.optional(MenuModel, {}),
  })
  .views(self => ({
    get branchId() {
      return self.selectedBranchId || RM.head(self.root.user.branchesId)
    },
    get queryProps() {
      const branchId = this.branchId
      if (!branchId) {
        return { skip: true }
      }
      return {
        variables: { branchId },
      }
    },
    getQueryProps<T extends Dictionary>(otherVariables: T) {
      const branchId = this.branchId
      if (!branchId) {
        return { skip: true }
      }
      return {
        variables: Object.assign({ branchId }, otherVariables),
      }
    },
  }))
  .actions(self => ({
    selectBranch(branchId: ID) {
      self.selectedBranchId = branchId
    },
  }))

export type TSettingsModel = typeof SettingsModel.Type
export type TSettingsModelProps = typeof SettingsModel.SnapshotType
