import { flow, types } from 'mobx-state-tree'
import * as R from 'ramda'
import { VError } from 'verror'

import {
  GCompanyBranch,
  GCreateZone,
  GDestroyZone,
  GUpdateZone,
  GUpdateZonePriority,
  GZone,
  GZonePriceRange,
} from '../../../graph/types'
import { generatePolygon } from '../../../helpers/gps'
import { RM } from '../../../helpers/ramda'
import { BaseModel } from '../../../models/BaseModel'
import { ZoneMapModel } from '../models/ZoneMapModel'
import {
  CreateZoneMutation,
  DestroyZoneMutation,
  UpdateZoneMutation,
  UpdateZonePriorityMutation,
} from './ZoneModel.graphql'

interface IVolatileProps {
  branch: Maybe<GCompanyBranch.Fragment>
}

export const ZoneModel = BaseModel.named('Zone')
  .props({
    route: '/settings/zones',
    map: types.optional(ZoneMapModel, {}),
    brandId: types.maybeNull(types.string),
  })
  .volatile<IVolatileProps>(() => ({
    branch: null,
  }))
  .views(self => ({
    get brand(): Maybe<GCompanyBranch.Brand> {
      return (
        (self.brandId &&
          self.root.user.brands!.find(RM.propEq('id', self.brandId))) ||
        null
      )
    },
    get hasBranchFilter() {
      return self.branch !== null
    },
    get branchFilterId(): MaybeID {
      return self.branch === null ? null : self.branch.id
    },
    get branchFilterPredicate() {
      if (self.branch !== null) {
        return R.propEq('id', self.branch.id)
      }
      return this.brand === null
        ? () => false
        : R.pathSatisfies(R.equals(this.brand.id), ['brand', 'id'])
    },
    get zoneFilterPredicate() {
      if (self.branch !== null) {
        return R.pathSatisfies(R.equals(self.branch.id), [
          'companyBranch',
          'id',
        ])
      }
      return this.brand === null
        ? () => false
        : R.pathSatisfies(R.equals(this.brand.id), [
            'companyBranch',
            'brand',
            'id',
          ])
    },
    getRouteToZone(zoneId: ID) {
      return `${self.route}/${zoneId}`
    },
    get branches() {
      if (!self.root.user.isReady) {
        return []
      }
      return self.root.user.branches!
    },
  }))
  .actions(self => ({
    setBranchFilter(branch: GCompanyBranch.Fragment) {
      self.branch = branch
      self.brandId = branch.brand.id
      self.map.panToBranch(branch)
    },
    setBrandFilter(
      brand: GCompanyBranch.Brand,
      brandBranches: GCompanyBranch.Fragment[],
    ) {
      self.brandId = brand.id
      if (!self.branch || self.branch.brand.id !== brand.id) {
        self.branch =
          (brandBranches.length === 1 && RM.head(brandBranches)) || null
      }
    },
    clearBranchFilter() {
      self.branch = null
    },
    clearBrandFilter() {
      self.brandId = null
      self.branch = null
    },
  }))
  .actions(self => ({
    createZone: flow(function*(onZoneCreate: (zoneId: ID) => void) {
      if (self.branch === null) {
        throw new VError('Called createZone without branch being set')
      }
      const points = generatePolygon({
        center: self.branch.gps,
        metersRadius: 1000,
        pointsCount: 5,
      })
      const variables: GCreateZone.Variables = {
        branchId: self.branch.id,
        points,
      }
      const { data } = yield self.client.mutate({
        mutation: CreateZoneMutation,
        variables,
        updateQueries: {
          GZones: (prev, { mutationResult }) => {
            const zone = (mutationResult.data as GCreateZone.Mutation)
              .createZone
            if (zone) {
              return {
                ...prev,
                zones: prev.zones.concat(zone),
              }
            }
            return prev
          },
        },
      })
      if (data) {
        const zone = (data as GCreateZone.Mutation).createZone
        onZoneCreate(zone.id)
      }
    }),
    // tslint:disable-next-line:no-any
    async saveZone(zoneId: ID, values: any) {
      const variables: GUpdateZone.Variables = {
        zoneId,
        update: {
          name: values.name,
          enabled: values.enabled,
          companyBranch: values.companyBranch.id,
          zoneColor: values.zoneColor.id,
          minimalOrderPrice: {
            value: Number(values.minimalOrderPrice.value) || 0,
          },
          priceRangeRules: values.priceRangeRules
            .map(normalizePriceRangeRule)
            .filter(R.reject(R.pathEq(['price', 'value'], null))),
          geoPolygon: self.map.currentZonePoints || undefined,
        },
      }
      await self.client.mutate({
        mutation: UpdateZoneMutation,
        variables,
        updateQueries: {
          GZones: (prev, { mutationResult }) => {
            const zoneIdx = prev.zones.findIndex(R.propEq('id', zoneId))
            const zone = (mutationResult.data as GUpdateZone.Mutation)
              .updateZone
            return {
              ...prev,
              zones: R.update(zoneIdx, zone, prev.zones),
            }
          },
        },
      })
      self.map.clearZonePoints()
    },
    destroyZone(zoneId: ID) {
      if (!zoneId) {
        return
      }
      const variables: GDestroyZone.Variables = {
        zoneId,
      }
      self.client.mutate({
        mutation: DestroyZoneMutation,
        variables,
        updateQueries: {
          GZones: prev => {
            return {
              ...prev,
              zones: R.reject(R.propEq('id', zoneId), prev.zones),
            }
          },
        },
      })
    },
    changeZonePriority: flow(function*(
      zone: GZone.Fragment,
      oldIndex: number,
      newIndex: number,
    ) {
      if (!zone || oldIndex === newIndex) {
        return
      }
      const offset = oldIndex - newIndex

      const variables: GUpdateZonePriority.Variables = {
        zoneId: zone.id,
        offset,
      }
      yield self.client.mutate({
        mutation: UpdateZonePriorityMutation,
        variables,
        optimisticResponse: {
          __typename: 'Mutation',
          updateZonePriority: {
            __typename: 'Zone',
            id: zone.id,
            priority: zone.priority + offset,
          },
        },
      })
    }),
  }))

function numberOrNull(value: string | number | null) {
  if (value === null) {
    return null
  }
  const num = Number(value)
  return Number.isNaN(num) ? null : num
}

function normalizePriceRangeRule(range: GZonePriceRange.Fragment) {
  return {
    price: { value: numberOrNull(range.price.value) },
    toPrice: { value: numberOrNull(range.toPrice.value) || -1 },
  }
}

export type TZoneModel = typeof ZoneModel.Type
