import Color from 'color'
import styled, { css } from 'styled-components'

import { Colors, Radiuses } from '../styling'

interface IProps {
  color?: string
  textColor?: string
  padding?: number
  stretchToParent?: boolean
}

export const Card = styled.div`
  background: ${getBackgroundColor};
  color: ${getTextColor};
  border-radius: ${Radiuses.Card};
  padding: ${getPadding}rem;
  margin: 0 0 0.5rem 0;
  ${applyStretchToParentStyle};
`

Card.displayName = 'Card'

function getPadding({ padding = 0.5 }: IProps) {
  return padding
}

function getBackgroundColor({ color }: IProps) {
  return Color(color || Colors.greyLight)
    .alpha(0.8)
    .lighten(0.4)
    .string()
}

function getTextColor(props: IProps) {
  return props.textColor || Colors.black
}

function applyStretchToParentStyle({ stretchToParent = false }: IProps) {
  if (!stretchToParent) {
    return ''
  }
  return css`
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  `
}
