import { types } from 'mobx-state-tree'

import { OrderListModel } from './OrderListModel'
import { OrderMapModel } from './OrderMapModel'
import { OrderStateModel } from './OrderStateModel'

export const OrderModel = types.model('Order', {
  list: types.optional(OrderListModel, {}),
  map: types.optional(OrderMapModel, {}),
  state: types.optional(OrderStateModel, {}),
})

export type TOrderModel = typeof OrderModel.Type
