import React from 'react'
import { Layout } from 'react-grid-layout'

import { IPageLayoutProps, PageLayout } from '../molecules/PageLayout'

interface IFullPageLayoutProps extends IPageLayoutProps {
  width?: number
}

function getLayout(width: number): Layout[] {
  return [{ i: 'full', x: 0, y: 1, w: width, h: 10 }]
}

export const FullPageLayout: React.SFC<IFullPageLayoutProps> = ({
  children,
  width = 16,
  ...props
}) => (
  <PageLayout layouts={getLayout(width)} {...props}>
    <div key="full">{children}</div>
  </PageLayout>
)
