import { getQueryErrors } from '../../../graph/errorHandler'
import {
  GMenuStockCreate,
  GMenuStockDelete,
  GMenuStockUpdate,
} from '../../../graph/types'
import { TStockFormValues } from '../organisms/StockFormLoader'
import { MenuStockListQuery } from '../organisms/StockList.graphql'
import { MenuBaseModel } from './MenuBaseModel'
import {
  MenuStockCreateMutation,
  MenuStockDeleteMutation,
  MenuStockUpdateMutation,
} from './MenuStockModel.graphql'

export const MenuStockModel = MenuBaseModel.named('MenuStock').actions(
  self => ({
    createItem(formValues: TStockFormValues) {
      const variables: GMenuStockCreate.Variables = {
        stockItem: {
          companyBranch: self.branchId,
          ...getFormVariables(formValues),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuStockCreateMutation,
          variables,
          refetchQueries: [
            {
              query: MenuStockListQuery,
              variables: {
                branchId: self.branchId,
              },
            },
          ],
        }),
      )
    },
    updateItem(formValues: TStockFormValues) {
      const variables: GMenuStockUpdate.Variables = {
        stockId: formValues.id,
        stockItem: {
          ...getFormVariables(formValues),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuStockUpdateMutation,
          variables,
        }),
      )
    },
    async deleteItem(stockId: ID) {
      const variables: GMenuStockDelete.Variables = {
        stockId,
      }
      await self.client.mutate({
        mutation: MenuStockDeleteMutation,
        variables,
        refetchQueries: [
          {
            query: MenuStockListQuery,
            variables: {
              branchId: self.branchId,
            },
          },
        ],
      })
    },
  }),
)

function getFormVariables(formValues: TStockFormValues) {
  return {
    code: formValues.code,
    label: formValues.nameLabel,
    note: formValues.note,
    plu: formValues.plu,
    allergens: formValues.allergens,
    wareUnit: formValues.wareUnit!,
    allowNegative: formValues.allowNegative,
  }
}

export type TMenuStockModel = typeof MenuStockModel.Type
