import { getParent } from 'mobx-state-tree'

import { BaseModel } from '../../../models/BaseModel'
import { TSettingsModel } from './SettingsModel'

export const SettingsBaseModel = BaseModel.named('SettingsBase').views(
  self => ({
    get settings() {
      return getParent<TSettingsModel>(self)
    },
  }),
)

export type TSettingsBaseModel = typeof SettingsBaseModel.Type
export type TSettingsBaseModelProps = typeof SettingsBaseModel.SnapshotType
