import React from 'react'
import { render } from 'react-dom'

import { App } from './App'
import { RootProvider } from './atoms/RootProvider'
import { Router } from './atoms/Router'
import { installSentry } from './helpers/sentry'
import { I18nManager } from './i18n'

const Main = () => (
  <RootProvider>
    <I18nManager>
      <Router>
        <App />
      </Router>
    </I18nManager>
  </RootProvider>
)

installSentry(() => {
  const rootElement = document.getElementById('root')
  render(<Main />, rootElement)
  // render(<StrictMode><Main /></StrictMode>, rootElement)
})
