import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GSpecificBusinessHours } from '../../../graph/types'

export const SpecificBusinessHourFragment = gql`
  fragment GSpecificBusinessHour on BusinessHour {
    id
    companyBranch {
      id
    }
    specificDate
    specificDateLocalized
    closed
    closedNote
    openAt
    closeAt
  }
`

export const QuerySpecificBusinessHours = buildQuery<
  GSpecificBusinessHours.Query,
  GSpecificBusinessHours.Variables
>(gql`
  query GSpecificBusinessHours($branchId: ID!) {
    businessHours(companyBranch: $branchId, specific: true) {
      ...GSpecificBusinessHour
    }
  }
  ${SpecificBusinessHourFragment}
`)
