import { getParent } from 'mobx-state-tree'

import { BaseModel } from '../../../models/BaseModel'
import { TMenuModel } from './MenuModel'

export const MenuBaseModel = BaseModel.named('MenuBase').views(self => ({
  get branchId() {
    return getParent<TMenuModel>(self).settings.branchId!
  },
}))

export type TMenuBaseModel = typeof MenuBaseModel.Type
export type TMenuBaseModelProps = typeof MenuBaseModel.SnapshotType
