import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { Icon } from '../atoms/Icon'
import { WithRoot } from '../atoms/WithRoot'
import { GLogoutWidget } from '../graph/types'
import { i18n } from '../i18n'
import { Colors } from '../styling'
import { QueryLogoutWidget } from './LogoutWidget.graphql'

const LogoutWidgetStyled = styled.div`
  margin-left: 1rem;
  color: ${Colors.greyLight};
  &:hover {
    color: ${Colors.white};
  }
`
const LoggedUserNameStyled = styled.span`
  color: ${Colors.greyLight};
  text-decoration: none;
  font-weight: lighter;
  position: relative;
  top: 0.6rem;
  display: inline-block;
  max-width: 10rem;
  text-align: right;
`

const IconStyled = styled.span`
  padding: 0.5rem 0.5rem 0 0.5rem;
  display: inline-block;
  vertical-align: top;
`

export const LogoutWidget: React.SFC = () => (
  <WithRoot
    render={({ auth }) => (
      <QueryLogoutWidget
        variables={{ id: auth.userId }}
        renderLoading={() => null}
        render={({ user }) => (
          <LogoutWidgetStyled data-testid="logout-widget">
            <Link to="/logout">
              <LoggedUserNameStyled>
                {getNameDisplay(user)}
              </LoggedUserNameStyled>
              <IconStyled>
                <Icon iconName="exit_to_app" />
              </IconStyled>
            </Link>
          </LogoutWidgetStyled>
        )}
      />
    )}
  />
)

function getNameDisplay(user: GLogoutWidget.User) {
  if (user) {
    return `${user.firstName} ${user.lastName}`
  } else {
    return i18n.t`Not logged in`
  }
}
