import { types } from 'mobx-state-tree'

import {
  DateTime,
  GCreateSpecificBusinessHour,
  GDeleteSpecificBusinessHour,
  Time,
} from '../../../graph/types'
import { formatTime } from '../../../helpers/formats'
import { RM } from '../../../helpers/ramda'
import { BaseModel } from '../../../models/BaseModel'
import {
  CreateSpecificBusinessHour,
  DeleteSpecificBusinessHour,
} from './BusinessHoursModel.graphql'

export enum ESpecificHourMode {
  TIMESPAN = 'TIMESPAN',
  FULLDAY = 'FULLDAY',
}

export interface ISpecificFormData {
  date: DateTime
  openAt: Time
  closeAt: Time
  note: string
}

const byIdPredicate = RM.complement(RM.propEq('id'))

export const BusinessHoursModel = BaseModel.named('BusinessHours')
  .props({
    specificMode: types.optional(
      types.enumeration(['TIMESPAN', 'FULLDAY']),
      ESpecificHourMode.TIMESPAN,
    ),
  })
  .actions(self => ({
    changeSpecificMode(mode: ESpecificHourMode) {
      self.specificMode = mode
    },
    submitSpecificHour(values: ISpecificFormData) {
      const variables: GCreateSpecificBusinessHour.Variables = {
        input: {
          companyBranch: self.root.settings.branchId!,
          specificDate: values.date,
          closed: self.specificMode === ESpecificHourMode.FULLDAY,
          openAt: values.openAt && formatTime(values.openAt),
          closeAt: values.closeAt && formatTime(values.closeAt),
          closedNote: values.note,
        },
      }
      self.client.mutate<GCreateSpecificBusinessHour.Mutation>({
        mutation: CreateSpecificBusinessHour,
        variables,
        updateQueries: {
          GSpecificBusinessHours: (prev, { mutationResult }) => {
            if (mutationResult.data) {
              return {
                businessHours: prev.businessHours.concat(
                  mutationResult.data.result,
                ),
              }
            }
            return prev
          },
        },
      })
    },
    deleteSpecificHour(businessHourId: ID) {
      const variables: GDeleteSpecificBusinessHour.Variables = {
        id: businessHourId,
      }
      self.client.mutate({
        mutation: DeleteSpecificBusinessHour,
        variables,
        updateQueries: {
          GSpecificBusinessHours: (prev, { mutationResult }) => {
            if (mutationResult.data) {
              return {
                businessHours: prev.businessHours.filter(
                  byIdPredicate(mutationResult.data.result.id),
                ),
              }
            }
            return prev
          },
        },
      })
    },
  }))

export type TBusinessHoursModel = typeof BusinessHoursModel.Type
export type TBusinessHoursModelProps = typeof BusinessHoursModel.SnapshotType
