import gql from 'graphql-tag'

import { buildQuery } from '../graph/buildQuery'
import { GLogoutWidget } from '../graph/types'

export const QueryLogoutWidget = buildQuery<
  GLogoutWidget.Query,
  GLogoutWidget.Variables
>(gql`
  query GLogoutWidget($id: ID!) {
    user(id: $id) {
      id
      firstName
      lastName
    }
  }
`)
