import Color from 'color'

import { RM } from './helpers/ramda'

export enum Colors {
  yellow = '#FFDB2B',
  pink = '#FF2B95',
  blue = '#2441FF',
  red = '#FF1C1C',
  green = '#179B12',
  turquoise = '#2BFFFF',
  orange = '#FF862B',
  violet = '#6D4F8C',
  black = '#000000',
  greyDarkest = '#080808',
  greyDark = '#2C2C2C',
  greyMiddle = '#939393',
  greyLight = '#EDEDED',
  white = '#FFFFFF',
}

export const applyAlphaToColor = RM.curry((color: Colors, alphaValue: number) =>
  Color(color)
    .alpha(alphaValue)
    .string(),
)

export enum Radiuses {
  Card = '0.5rem',
  Button = '0.5rem',
  TextComponent = '0.25rem',
}

export enum Spacing {
  MarginAroundFormComponents = '0.1rem',
  Small = '0.25rem',
  Middle = '0.5rem',
  LayoutGridCellMargin = '0.5rem',
  ButtonHeight = '3.4375rem',
  DefaultInputWidth = 20,
  DefaultSizeUnit = 'rem',
}
