import React from 'react'
import { Redirect } from 'react-router'

import { WithRoot } from '../atoms/WithRoot'
import { RedirectToLogin } from './RedirectToLogin'

export const RedirectAfterLogout = () => (
  <WithRoot
    observe={false}
    render={root => {
      if (root.auth.isAuthenticated) {
        root.auth.invalidate()
        return <RedirectToLogin useOrigin={false} />
      }
      return <Redirect to="/" />
    }}
  />
)
