import { ApolloQueryResult } from 'apollo-client'
import { FetchResult } from 'apollo-link'
import _ from 'lodash'
import { autorun, IReactionDisposer } from 'mobx'
import { flow, Instance } from 'mobx-state-tree'
import { ExecutionResult } from 'react-apollo'
import { VError } from 'verror'

import {
  DeliveryTypeEnum,
  GCreateOrder,
  GOrderVam,
  GUpdateOrder,
  GValidateLocation,
  MAcceptOrder,
  MCancelOrder,
} from '../../../graph/types'
import { faker } from '../../../helpers/faker'
import { reportError } from '../../../helpers/sentry'
import { i18n } from '../../../i18n'
import { BaseModel } from '../../../models/BaseModel'
import {
  AcceptOrderMutation,
  CancelOrderMutation,
  CreateOrderMutation,
  UpdateOrderMutation,
  ValidateLocationQuery,
} from './CreateOrderModel.graphql'

export interface IOrderFormValues {
  deliveryType: DeliveryTypeEnum.MESSENGER | DeliveryTypeEnum.PICKUP
  phone?: string
  firstName?: string
  lastName?: string
  email?: string
  note?: string
}

interface IVolatileProps {
  orderId: MaybeID
  callingPhone?: string
  callingEventId?: ID
  location?: GeoPoint
  values: IOrderFormValues
  fakeValues?: Partial<IOrderFormValues>
  fakeAddress?: string
  vamCustomer: Maybe<GOrderVam.VamCustomer>
  notDeliveryLocation: boolean
  isSubmitting: boolean
  submitErrors: Maybe<ReadonlyArray<string>>
  scrollMenuToCategoryId: MaybeID
}

export const CreateOrderModel = BaseModel.named('CreateOrder')
  .volatile<IVolatileProps>(getDefaultProps)
  .views(self => ({
    get branchId() {
      return self.root.user.currentBranchId!
    },
    get gps() {
      return self.location &&
        self.values.deliveryType === DeliveryTypeEnum.MESSENGER
        ? { ...self.location }
        : { ...self.root.user.currentBranchGps! }
    },
    get phone() {
      return self.values.phone || self.callingPhone
    },
    get formValues() {
      if (self.vamCustomer !== null && self.vamCustomer.isFound) {
        const {
          firstName,
          lastName,
          email,
          phone,
          lastOrder,
        } = self.vamCustomer
        return _.defaults(
          { ...self.values },
          {
            phone,
            firstName,
            lastName,
            email,
            note: (lastOrder && lastOrder.note) || '',
          },
        )
      }
      if (self.fakeValues) {
        const { firstName, lastName, email, phone, note } = self.fakeValues
        return _.defaults(
          { ...self.values },
          {
            phone,
            firstName,
            lastName,
            email,
            note,
          },
        )
      }
      return _.defaults({ ...self.values }, { phone: self.callingPhone || '' })
    },
    get deliveryType() {
      return self.values.deliveryType
    },
    get isDelivery() {
      return self.values.deliveryType === DeliveryTypeEnum.MESSENGER
    },
    get hasMultipleVamLocations() {
      return self.vamCustomer !== null && self.vamCustomer.locations.length > 1
    },
    get canSubmit() {
      return Boolean(self.orderId && !self.isSubmitting)
    },
    get hasSubmitErrors() {
      return Boolean(self.submitErrors && self.submitErrors.length > 0)
    },
    get askForCancel() {
      return self.orderId !== null
    },
    get cartRefreshKey() {
      if (self.orderId === null) {
        return self.values.deliveryType
      }
      return `${self.orderId}-${self.values.deliveryType}`
    },
  }))
  .actions(self => {
    const submitFailed = (errors?: string[]) => {
      self.isSubmitting = false
      self.submitErrors = errors || [
        i18n.t`Order submit has failed. Please try again later.`,
      ]
    }
    let updatesWatch: Maybe<IReactionDisposer>
    const resetOrder = () => {
      self.orderId = null
      if (updatesWatch) {
        updatesWatch()
        updatesWatch = null
      }
    }
    const resetModel = () => {
      Object.assign(self, getDefaultProps())
    }
    const ensureOrder = flow<ID>(function*() {
      if (self.orderId === null) {
        const variables: GCreateOrder.Variables = {
          branchId: self.branchId,
          deliveryType: self.values.deliveryType,
          gps: self.gps,
          callEventId: self.callingEventId,
        }
        const result: ExecutionResult<
          GCreateOrder.Mutation
        > = yield self.client.mutate({
          mutation: CreateOrderMutation,
          variables,
        })
        if (result.data) {
          self.orderId = result.data.createOrder.id
        }
      }
      return self.orderId
    })
    const acceptOrder = flow(function*(formValues: IOrderFormValues) {
      const variables: MAcceptOrder.Variables = {
        orderId: self.orderId!,
        customer: {
          firstName: formValues.firstName,
          lastName: formValues.lastName,
          emails: formValues.email ? [{ email: formValues.email }] : null,
          phones: formValues.phone ? [{ phone: formValues.phone }] : null,
        },
        deliveryType: formValues.deliveryType,
        gps: self.gps,
        note: formValues.note,
      }

      self.log('submit order: ', variables)

      self.isSubmitting = true
      let result: FetchResult

      try {
        result = yield self.client.mutate({
          mutation: AcceptOrderMutation,
          variables,
        })
      } catch (err) {
        submitFailed()
        throw reportError(new VError('order create submit failed: %j', err))
      }

      if ((result.errors && result.errors.length) || !result.data) {
        submitFailed()
        throw reportError(
          new VError('order create submit failed: %j', result.errors),
        )
      }

      const submitResult: MAcceptOrder.FinishOrder = result.data.finishOrder
      if (submitResult.userErrors && submitResult.userErrors.length) {
        submitFailed(submitResult.userErrors)
        throw reportError(
          new VError(
            'order create validation errors: %j',
            submitResult.userErrors,
          ),
        )
      }

      resetModel()
    })
    const cancelOrder = flow(function*() {
      if (!self.orderId) {
        resetModel()
        return
      }
      const variables: MCancelOrder.Variables = {
        orderId: self.orderId,
      }
      self.client.mutate({ mutation: CancelOrderMutation, variables })
      resetModel()
    })

    autorun(() => {
      if (self.orderId === null) {
        return
      }
      const variables: GUpdateOrder.Variables = {
        orderId: self.orderId,
        deliveryType: self.formValues.deliveryType,
        gps: self.gps,
      }
      self.client.mutate({
        mutation: UpdateOrderMutation,
        variables,
      })
    })

    return {
      submitFailed,
      ensureOrder,
      acceptOrder,
      cancelOrder,
      resetOrder,
    }
  })
  .actions(self => {
    const validateLocation = flow(function*() {
      if (!self.location) {
        self.notDeliveryLocation = false
        return
      }
      const { latitude, longitude } = self.location
      const variables: GValidateLocation.Variables = {
        branchId: self.branchId,
        latitude,
        longitude,
      }
      const result: ApolloQueryResult<
        GValidateLocation.Query
      > = yield self.client.query({ query: ValidateLocationQuery, variables })

      self.notDeliveryLocation = !result.data.resolveLocation.inDeliveryLocation
    })
    return {
      useLocation({ latitude, longitude }: GeoPoint) {
        self.location = { latitude, longitude }
        validateLocation()
      },
    }
  })
  .actions(self => ({
    userIsCalling(phone: string, eventId: MaybeID) {
      self.callingPhone = phone
      if (eventId) {
        self.callingEventId = eventId
      }
    },
    scrollToCategory(categoryId: ID) {
      self.scrollMenuToCategoryId = categoryId
    },
    updateFormValues(
      values: IOrderFormValues,
      touched: { [K in keyof IOrderFormValues]?: boolean },
    ) {
      self.log('touched', touched)
      self.values = {
        deliveryType: values.deliveryType,
        ..._.pick(values, Object.keys(touched)),
      }
      self.log('updated values: ', self.values)
    },
    updateFromVam(vamCustomer: Maybe<GOrderVam.VamCustomer>) {
      self.vamCustomer = vamCustomer
      self.log('vam customer: ', vamCustomer)
    },
    updateFormWithFakeValues() {
      self.fakeValues = {
        firstName: faker.name.firstName(),
        lastName: faker.name.lastName(),
        email: faker.internet.email(),
        phone: faker.phone.phoneNumberFormat(4),
        note: faker.lorem.sentence().slice(0, 50 - 1),
      }
      self.fakeAddress = 'Opletalova 334, Hradec Králové, Česko'
    },
  }))

function getDefaultProps(): IVolatileProps {
  return {
    orderId: null,
    callingPhone: undefined,
    callingEventId: undefined,
    values: {
      deliveryType: DeliveryTypeEnum.MESSENGER,
      phone: undefined,
      email: undefined,
      firstName: undefined,
      lastName: undefined,
      note: undefined,
    },
    fakeAddress: undefined,
    fakeValues: undefined,
    location: undefined,
    vamCustomer: null,
    notDeliveryLocation: false,
    isSubmitting: false,
    submitErrors: null,
    scrollMenuToCategoryId: null,
  }
}

export interface TCreateOrderModel extends Instance<typeof CreateOrderModel> {}
