import { Catalog, setupI18n } from '@lingui/core'
import { I18nProvider } from '@lingui/react'
import * as detector from 'locales-detector'
import { observer } from 'mobx-react'
import React from 'react'

import Pacman from './atoms/Pacman'
import { useRoot } from './atoms/RootProvider'
import { logLang } from './helpers/logger'
import { changeNumeralLocale } from './locale/numeral'

export const i18n = setupI18n()

export const fallbackLanguage = 'en'
export const supportedLanguages = ['cs', 'sk', 'en']

export const I18nManager = observer(({ children }) => {
  const { user } = useRoot()
  if (!user.language) {
    return <Pacman />
  }
  return (
    <I18nProvider language={user.language} i18n={i18n}>
      {React.Children.only(children)}
    </I18nProvider>
  )
})

export function detectLanguage() {
  const resolver = new detector.LocaleResolver(
    [new detector.NavigatorDetector()],
    [new detector.LanguageOnlyTransformer()],
  )
  const languages = resolver.getLocales()
  logLang('detected languages %s', languages.join('; '))
  const supported = languages.find(loc => supportedLanguages.includes(loc))
  return supported || fallbackLanguage
}

export async function changeLanguage(lang: string) {
  if (!supportedLanguages.includes(lang)) {
    logLang('language %s is not supported')
    return false
  }
  await loadCatalog(lang)
  i18n.activate(lang)
  await changeNumeralLocale(lang)
  logLang('changed to %s', lang)
  return true
}

async function loadCatalog(lang: string) {
  let catalog: Catalog
  if (process.env.NODE_ENV !== 'production') {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `@lingui/loader!./locale/${lang}/messages.po`
    )
  } else {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `./locale/${lang}/messages.ts`
    )
  }
  i18n.load({ [lang]: catalog })
  logLang('loaded catalog %s', lang)
}
