import React from 'react'
import styled from 'styled-components'

import { Colors } from '../styling'

interface IProps {
  iconName: string
  color?: string
  fontSize?: number
  fontSizeUnit?: string
}

const getFontSize = ({ fontSize = 1.5, fontSizeUnit = 'rem' }: IProps) => {
  return fontSize + fontSizeUnit
}

const IconSpanStyled = styled.span`
  font-size: ${getFontSize} !important;
  color: ${(props: IProps) => props.color};
`

export const Icon: React.SFC<IProps> = ({
  iconName,
  color = Colors.white,
  fontSize,
  fontSizeUnit,
}) =>
  iconName ? (
    <IconSpanStyled
      color={color}
      fontSize={fontSize}
      fontSizeUnit={fontSizeUnit}
      iconName={iconName}
      className="material-icons"
    >
      {iconName}
    </IconSpanStyled>
  ) : null
