import React, { ErrorInfo } from 'react'
import {
  DeveloperErrorBoundary,
  ProductionErrorBoundary,
} from 'react-error-guard'

import { reportRenderingError } from '../helpers/sentry'

interface IProps {
  prod: boolean
}

export const ErrorContainer: React.SFC<IProps> = ({
  children,
  prod = false,
}) => {
  if (prod) {
    return (
      <ProductionErrorBoundary
        dispatchErrorReporting={(data: { error: Error; info: ErrorInfo }) => {
          reportRenderingError(data.error, data.info)
        }}
      >
        {children}
      </ProductionErrorBoundary>
    )
  } else {
    return <DeveloperErrorBoundary>{children}</DeveloperErrorBoundary>
  }
}
