import React from 'react'
import { Route, RouteProps } from 'react-router'

import { useRoot } from '../atoms/RootProvider'
import { NotFoundPage } from '../pages/NotFoundPage'
import { LazyRoute } from './LazyRoute'
import { RedirectToLogin } from './RedirectToLogin'

interface ISecureRouteProps extends RouteProps {
  redirect?: boolean
  lazy?: boolean
}

export const SecureRoute: React.SFC<ISecureRouteProps> = ({
  redirect = true,
  lazy = true,
  ...props
}) => {
  const { auth } = useRoot()
  if (!auth.isAuthenticatedUser) {
    return redirect ? <RedirectToLogin /> : <NotFoundPage />
  }
  if (props.component && typeof props.component === 'object') {
    const { component, path, exact } = props
    return <LazyRoute path={path} exact={exact} component={component} />
  }
  return <Route {...props} />
}
