import gql from 'graphql-tag'

import { AllergenFragment } from '../graph/AllergenFragment.graphql'
import { WareUnitFragment } from '../graph/WareUnitFragment.graphql'

export const MenuStockItemFragment = gql`
  fragment GMenuStockItem on StockItem {
    id
    code
    note
    plu
    quantity
    nameLabel: label
    allergens {
      ...GAllergen
    }
    wareUnit {
      ...GWareUnit
    }
  }
  ${WareUnitFragment}
  ${AllergenFragment}
`

export const MenuStockCreateMutation = gql`
  mutation GMenuStockCreate($stockItem: InputStockItemCreate!) {
    createStockItem(stockItem: $stockItem) {
      ...GMenuStockItem
    }
  }
  ${MenuStockItemFragment}
`

export const MenuStockUpdateMutation = gql`
  mutation GMenuStockUpdate($stockId: ID!, $stockItem: InputStockItemUpdate!) {
    updateStockItem(id: $stockId, stockItem: $stockItem) {
      ...GMenuStockItem
    }
  }
  ${MenuStockItemFragment}
`

export const MenuStockDeleteMutation = gql`
  mutation GMenuStockDelete($stockId: ID!) {
    destroyStockItem(id: $stockId) {
      id
    }
  }
`
