import React from 'react'
import RGL, { Layout, WidthProvider } from 'react-grid-layout'

const ReactGridLayout = WidthProvider(RGL)

interface IGridLayoutProps {
  layouts: Layout[]
}

export const GridLayout: React.SFC<IGridLayoutProps> = ({
  children,
  layouts,
}) => (
  <ReactGridLayout
    className="layout"
    layout={layouts}
    cols={16}
    rowHeight={55}
    margin={[8, 8]}
    autoSize={false}
    isDraggable={false}
    isResizable={false}
    compactType={null}
    measureBeforeMount={true}
  >
    {children}
  </ReactGridLayout>
)
