import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GMenuStockList } from '../../../graph/types'
import { MenuStockItemFragment } from '../models/MenuStockModel.graphql'

export const MenuStockListQuery = gql`
  query GMenuStockList($branchId: ID!) {
    stockItems(companyBranch: $branchId) {
      ...GMenuStockItem
    }
  }
  ${MenuStockItemFragment}
`

export const QueryMenuStockList = buildQuery<
  GMenuStockList.Query,
  GMenuStockList.Variables
>(MenuStockListQuery)
