import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GCategoryList } from '../../../graph/types'

export const MenuCategoryListFragment = gql`
  fragment GMenuCategoryListItem on WareCategory {
    id
    nameLabel
    internalNoteLabel
    countRecipes
    visible: enabled
    categoryType: wareCategoryType {
      enum
    }
  }
`

export const CategoryListQuery = gql`
  query GCategoryList($branchId: ID!, $categoryType: WareCategoryTypeEnum!) {
    wareCategories(companyBranch: $branchId, wareCategoryType: $categoryType) {
      ...GMenuCategoryListItem
    }
  }
  ${MenuCategoryListFragment}
`

export const QueryCategoryList = buildQuery<
  GCategoryList.Query,
  GCategoryList.Variables
>(CategoryListQuery)
