import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { Statistics } from '../../../graph/types'

export const StatisticsQuery = gql`
  query Statistics($branches: [ID!]!, $dateFrom: Date!, $dateTo: Date!) {
    stats: statistics(
      branches: $branches
      dateFrom: $dateFrom
      dateTo: $dateTo
    ) {
      categoryStatisticsPlain
      dailyStatisticsPlain
      deliveryStatisticsPlain
    }
  }
`

export const CreateFinancialReport = gql`
  mutation GCreateFinancialReport($companyBranchId: ID!) {
    result: createFinancialReport(companyBranchId: $companyBranchId) {
      id
    }
  }
`

export const QueryStatistics = buildQuery<
  Statistics.Query,
  Statistics.Variables
>(StatisticsQuery)
