import gql from 'graphql-tag'

import { MenuRecipeFragment } from '../models/MenuRecipeModel.graphql'

export const MenuCategoryFormFragment = gql`
  fragment GMenuCategoryForm on WareCategory {
    id
    nameLabel
    internalNoteLabel
    visible: enabled
    categoryType: wareCategoryType {
      enum
    }
    pictogram
    recipes {
      ...GMenuRecipe
    }
  }
  ${MenuRecipeFragment}
`

export const MenuCategoryFormQuery = gql`
  query GMenuCategoryForm($categoryId: ID!) {
    wareCategory(id: $categoryId) {
      ...GMenuCategoryForm
    }
  }
  ${MenuCategoryFormFragment}
`
