import { Base64 } from 'js-base64'
import { Instance, types } from 'mobx-state-tree'

import { MPasswordChange } from '../graph/types'
import { identifyUser } from '../helpers/sentry'
import { LoginProviderModel } from '../segments/login/models/LoginProviderModel'
import { PasswordChangeMutation } from './AuthModel.graphql'
import { BaseModel } from './BaseModel'

interface IVolatileProps {
  tokenForPasswordChange: string
}

export const AuthModel = BaseModel.named('Auth')
  .props({
    userId: types.optional(types.string, ''),
    token: types.optional(types.string, ''),
    provider: types.optional(LoginProviderModel, {}),
  })
  .volatile<IVolatileProps>(self => ({
    tokenForPasswordChange: '',
  }))
  .views(self => ({
    get isAuthenticated() {
      return self.token.length > 0
    },
    get isAuthenticatedUser() {
      return this.isAuthenticated && self.userId.length > 0
    },
    get communicationToken() {
      return self.tokenForPasswordChange || self.token
    },
    get canCommunicate() {
      return this.isAuthenticated || self.tokenForPasswordChange.length > 0
    },
  }))
  .actions(self => ({
    authenticate(token: string, userId: string) {
      self.token = token
      self.userId = userId
      self.tokenForPasswordChange = ''
      self.log('authenticated with id %s', userId)
      identifyUser(userId)
    },
    invalidate() {
      self.token = ''
      self.userId = ''
      self.tokenForPasswordChange = ''
      self.client.cache.reset()
      self.log('invalidated authentication')
      identifyUser(null)
    },
  }))
  .actions(self => ({
    setForPasswordChange(temporaryB64EncodedToken: string) {
      self.invalidate()
      self.tokenForPasswordChange = Base64.decode(temporaryB64EncodedToken)
      self.log('using temp token %s', self.tokenForPasswordChange)
    },
    async changePassword(password: string) {
      const variables: MPasswordChange.Variables = {
        password,
      }
      const result = await self.client.mutate({
        mutation: PasswordChangeMutation,
        variables,
        fetchPolicy: 'no-cache',
      })

      const {
        updateUserPassword: { id, token },
      } = result.data as MPasswordChange.Mutation

      if (token && id) {
        self.invalidate()
        self.authenticate(token, id)
      }
    },
  }))
  .preProcessSnapshot(snapshot => {
    if (snapshot.userId) {
      identifyUser(snapshot.userId)
    }
    return snapshot
  })

export interface TAuthModel extends Instance<typeof AuthModel> {}
