import { GCompanyBranch, GZoneLocation } from '../../../graph/types'
import { gpsToLatLng } from '../../../helpers/gps'
import { MapModel } from '../../../models/MapModel'
import { ZoneLocationQuery } from '../Zones.graphql'

interface IVolatileProps {
  currentZonePoints: Maybe<GeoPoint[]>
}

export const ZoneMapModel = MapModel.named('ZoneMap')
  .volatile<IVolatileProps>(() => ({
    currentZonePoints: null,
  }))
  .actions(self => ({
    updateZonePoints(points: GeoPoint[]) {
      self.currentZonePoints = points
    },
    clearZonePoints() {
      self.currentZonePoints = null
    },
    async panToZone(zoneId: ID) {
      const { data } = await self.client.query({
        query: ZoneLocationQuery,
        variables: { zoneId },
      })
      if (data) {
        const {
          zone: {
            companyBranch: { gps },
          },
        } = data as GZoneLocation.Query
        if (self.mapRef) {
          self.mapRef.panTo(gpsToLatLng(gps))
        }
      }
    },
    async panToBranch(branch: GCompanyBranch.Fragment) {
      if (self.mapRef) {
        self.mapRef.panTo(gpsToLatLng(branch.gps))
      }
    },
  }))

export type TZoneMapModel = typeof ZoneMapModel.Type
