import { observable } from 'mobx'
import { getParent, types } from 'mobx-state-tree'
import { scroller } from 'react-scroll'

import { GOrderList } from '../../../graph/types'
import { BaseModel } from '../../../models/BaseModel'
import { TOrderModel } from './OrderModel'

export const OrderListModel = BaseModel.named('OrderList')
  .props({
    branches: types.optional(types.array(types.string), []),
  })
  .views(self => ({
    get branchesFilter() {
      return self.branches.length ? Array.from(self.branches) : []
    },
    get hasBranchFilter() {
      return self.branches.length > 0
    },
    get isSingleBranchSelected() {
      return self.branches.length === 1
    },
    get orderMap() {
      return getParent<TOrderModel>(self).map
    },
  }))
  .actions(self => ({
    clearBranchFilter() {
      self.branches.clear()
    },
    setBranchFilter(branches: ID[]) {
      self.branches.replace(branches)
    },
    panMapToOrder(order: GOrderList.Orders) {
      self.orderMap.panToOrder(order.id)
    },
  }))
  .extend(self => {
    const detailOpen = observable(new Map())

    return {
      views: {
        isOrderDetailOpen(orderId: string) {
          return detailOpen.has(orderId)
        },
      },
      actions: {
        toggleOrderDetail(order: GOrderList.Orders) {
          const orderId = order.id
          if (detailOpen.has(orderId)) {
            detailOpen.delete(orderId)
          } else {
            detailOpen.set(orderId, true)
            self.panMapToOrder(order)
          }
        },
        focusOrderDetail(orderId: ID) {
          if (!detailOpen.has(orderId)) {
            detailOpen.set(orderId, true)
          }
          setImmediate(() => {
            scroller.scrollTo(`order-${orderId}`, {
              smooth: 'easeInOutQuart',
              containerId: 'order-list',
              absolute: true,
              offset: -140,
            })
          })
        },
      },
    }
  })

export type TOrderListModel = typeof OrderListModel.Type
