import ActionCable from 'actioncable'
import { ApolloLink, Observable, Operation } from 'apollo-link'
import { getOperationDefinitionOrDie } from 'apollo-utilities'
import { print } from 'graphql/language'
import { computed } from 'mobx'
import { VError } from 'verror'

import { appConfig } from '../config'
import { TAuthModel } from '../models/AuthModel'

const getChannelId = () =>
  Math.round(Date.now() + Math.random() * 100000).toString(16)

export default function createSubscriptionLink(auth: TAuthModel) {
  const cableUrl = computed(
    () => `${appConfig.endpoint}/cable?token=${auth.token}`,
  )

  let cable
  const channelName = 'GraphqlChannel'
  const actionName = 'execute'
  const connectionParams = {}

  return new ApolloLink(operation => {
    if (!auth.isAuthenticated) {
      throw new VError(
        `Executing operation ${operation.operationName} before authenticated`,
        { operation },
      )
    }
    if (!cable) {
      cable = ActionCable.createConsumer(cableUrl.get())
    }
    return new Observable(observer => {
      const channelId = getChannelId()

      const subscription = cable.subscriptions.create(
        Object.assign(
          {},
          {
            channel: channelName,
            channelId: channelId,
          },
          connectionParams,
        ),
        {
          connected: function() {
            this.perform(actionName, {
              query: operation.query ? print(operation.query) : null,
              variables: operation.variables,
              // @ts-ignore
              operationId: operation.operationId,
              operationName: operation.operationName,
            })
          },
          received: function(payload: {
            result: { data: object }
            more: boolean
          }) {
            if (payload.result.data) {
              observer.next(payload.result)
            }

            if (!payload.more) {
              this.unsubscribe()
              observer.complete()
            }
          },
        },
      )

      return subscription
    })
  })
}

export function isSubscriptionOperation({ query }: Operation) {
  const { operation } = getOperationDefinitionOrDie(query)
  return operation === 'subscription'
}
