import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { PriceFragment } from '../../../graph/fragments'
import { GMenuRecipeFormAssoc } from '../../../graph/types'
import { MenuCategoryListFragment } from './CategoryList.graphql'

export const MenuRecipePriceFragment = gql`
  fragment GMenuRecipePrice on RecipePrice {
    id
    priceType: warePriceType {
      enum
    }
    price {
      ...GPrice
    }
    vat {
      id
    }
  }
  ${PriceFragment}
`

export const MenuAssocRecipeFragment = gql`
  fragment GMenuAssocRecipe on Recipe {
    id
    nameLabel
  }
`

export const MenuRecipeFormQuery = gql`
  query GMenuRecipeForm($recipeId: ID!) {
    recipe(id: $recipeId) {
      id
      code
      image
      nameLabel
      internalNote
      descriptionLabel
      visible: enabled
      recipeType: wareCategoryType {
        enum
      }
      sourcePrices: prices {
        ...GMenuRecipePrice
      }
      sideDishes: sideDishesWareCategories {
        ...GMenuCategoryListItem
      }
      categories: wareCategories {
        ...GMenuCategoryListItem
      }
      sourceAssociatedRecipes: associatedRecipes {
        id
        priceType: warePriceType {
          enum
        }
        recipe: associatedRecipe {
          ...GMenuAssocRecipe
        }
      }
      sourceIngredients: ingredients {
        id
        quantity
        stockItem {
          id
          label
          wareUnit {
            id
            code
          }
          allergens {
            id
            enum
          }
        }
      }
    }
  }
  ${MenuRecipePriceFragment}
  ${MenuCategoryListFragment}
  ${MenuAssocRecipeFragment}
`

export const MenuRecipeFormAssocQuery = gql`
  query GMenuRecipeFormAssoc($branchId: ID!) {
    recipes(companyBranch: $branchId) {
      ...GMenuAssocRecipe
    }
  }
  ${MenuAssocRecipeFragment}
`

export const MenuRecipeDefaultVat = gql`
  query GMenuRecipeDefaultVat($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      defaultVats {
        vatId: id
        priceType: warePriceType {
          enum
        }
      }
    }
  }
`

export const QueryMenuRecipeFormAssoc = buildQuery<
  GMenuRecipeFormAssoc.Query,
  GMenuRecipeFormAssoc.Variables
>(MenuRecipeFormAssocQuery)
