import { types } from 'mobx-state-tree'
import { IFormState } from 'react-form'

import { GCreateFinancialReport } from '../../../graph/types'
import { BaseModel } from '../../../models/BaseModel'
import { CreateFinancialReport } from './StatisticsModel.graphql'

interface IDateFilter {
  dateFrom: Date
  dateTo: Date
}

export const StatisticsModel = BaseModel.named('Statistics')
  .props({
    branches: types.optional(types.array(types.string), []),
  })
  .volatile<IDateFilter>(self => ({
    dateFrom: new Date(),
    dateTo: new Date(),
  }))
  .views(self => ({
    get branchesFilter() {
      return self.branches.length ? Array.from(self.branches) : []
    },
    get hasBranchFilter() {
      return self.branches.length > 0
    },
    get isSingleBranchSelected() {
      return self.branches.length === 1
    },
    get singleBranchFilter() {
      return self.branches[0]!
    },
  }))
  .actions(self => ({
    submitFilter(formState: IFormState<IDateFilter>) {
      self.dateFrom = new Date(formState.values.dateFrom)
      self.dateTo = new Date(formState.values.dateTo)
    },
    clearBranchFilter() {
      self.branches.clear()
    },
    setBranchFilter(branchId: ID) {
      self.branches.clear()
      self.branches.push(branchId)
    },
    createClosure() {
      if (self.isSingleBranchSelected) {
        const variables: GCreateFinancialReport.Variables = {
          companyBranchId: self.singleBranchFilter,
        }
        self.client.mutate({
          mutation: CreateFinancialReport,
          variables,
          refetchQueries: [`FinancialReportsCompanyBranch`],
        })
      }
    },
  }))

export type TStatisticsModel = typeof StatisticsModel.Type
