import gql from 'graphql-tag'

import { EmailFragment } from './EmailFragment.graphql'
import { PhoneFragment } from './PhoneFragment.graphql'

export const CustomerFragment = gql`
  fragment GCustomer on JsonCustomer {
    firstName
    lastName
    defaultEmail {
      ...GEmail
    }
    defaultPhone {
      ...GPhone
    }
  }
  ${EmailFragment}
  ${PhoneFragment}
`
