import { Instance, types } from 'mobx-state-tree'

import { AuthModel } from '../models/AuthModel'
import { ConfigModel } from '../models/ConfigModel'
import { TimingModel } from '../models/TimingModel'
import { UserModel } from '../models/UserModel'
import { CreateOrderModel } from '../segments/createorder/models/CreateOrderModel'
import { OrderModel } from '../segments/order/models/OrderModel'
import { StatisticsModel } from '../segments/overview/models/StatisticsModel'
import { SettingsModel } from '../segments/settings/models/SettingsModel'
import { ZoneModel } from '../segments/zones/models/ZoneModel'
import { BaseModel } from './BaseModel'

export const RootModel = BaseModel.named('Root')
  .props({
    auth: types.optional(AuthModel, {}),
    user: types.optional(UserModel, {}),
    order: types.optional(OrderModel, {}),
    createOrder: types.optional(CreateOrderModel, {}),
    zone: types.optional(ZoneModel, {}),
    timing: types.optional(TimingModel, {}),
    settings: types.optional(SettingsModel, {}),
    config: types.optional(ConfigModel, {}),
    statistics: types.optional(StatisticsModel, {}),
  })
  .views(self => ({
    get persistedModels() {
      return [
        self.auth,
        self.user,
        self.settings,
        self.zone,
        self.order,
        self.statistics,
      ]
    },
  }))

export interface TRootModel extends Instance<typeof RootModel> {}
