import { ApolloLink, FetchResult, Observable } from 'apollo-link'
import { computed } from 'mobx'
import VError from 'verror'

import { TAuthModel } from '../models/AuthModel'

export default function createAuthLink(auth: TAuthModel) {
  const headers = computed(() => ({
    Authorization: `Bearer ${auth.communicationToken}`,
  }))

  return new ApolloLink((operation, forward) => {
    if (!auth.canCommunicate) {
      throw new VError(
        `Executing operation ${operation.operationName} before authenticated`,
        { operation },
      )
    }

    const context = operation.getContext()
    operation.setContext({ ...context, headers: headers.get() })

    return new Observable<FetchResult>(observer => {
      const sub = forward!(operation).subscribe({
        error: networkError => {
          if (
            networkError.result &&
            networkError.result.code === 'INVALID_TOKEN'
          ) {
            auth.invalidate()
            return
          }
          observer.error(networkError)
        },
        next: observer.next.bind(observer),
        complete: observer.complete.bind(observer),
      })
      return () => sub.unsubscribe()
    })
  })
}
