import { types } from 'mobx-state-tree'

import { SettingsBaseModel } from '../../settings/models/SettingsBaseModel'
import { MenuCategoryModel } from './MenuCategoryModel'
import { MenuRecipeModel } from './MenuRecipeModel'
import { MenuStockModel } from './MenuStockModel'

export const MenuModel = SettingsBaseModel.named('Menu').props({
  category: types.optional(MenuCategoryModel, {}),
  recipe: types.optional(MenuRecipeModel, {}),
  stock: types.optional(MenuStockModel, {}),
})

export type TMenuModel = typeof MenuModel.Type
export type TMenuModelProps = typeof MenuModel.SnapshotType
