import React from 'react'

import { TUserModel } from '../models/UserModel'
import { WithRoot } from './RootProvider'

interface IProps {
  render(user: TUserModel): ReactNode
  observe?: boolean
}

export const WithUser: React.SFC<IProps> = ({ render, observe = true }) => (
  <WithRoot observe={observe} children={root => render(root.user)} />
)
