import gql from 'graphql-tag'

import { PriceFragment } from '../../../graph/fragments'

export const MenuRecipeFragment = gql`
  fragment GMenuRecipe on Recipe {
    id
    code
    visible: enabled
    nameLabel
    internalNote
    availableAmount
    prices {
      id
      priceType: warePriceType {
        enum
      }
      price {
        ...GPrice
        formattedValue
      }
    }
    recipeType: wareCategoryType {
      enum
    }
  }
  ${PriceFragment}
`

export const MenuRecipeVisibilityMutation = gql`
  mutation GMenuRecipeVisibility($recipeId: ID!, $visible: Boolean!) {
    updateRecipe(id: $recipeId, recipe: { enabled: $visible }) {
      id
      visible: enabled
    }
  }
`

export const MenuRecipeCreateMutation = gql`
  mutation GMenuRecipeCreate($recipe: InputRecipeCreate!) {
    createRecipe(recipe: $recipe) {
      ...GMenuRecipe
    }
  }
  ${MenuRecipeFragment}
`

export const MenuRecipeUpdateMutation = gql`
  mutation GMenuRecipeUpdate($recipeId: ID!, $recipe: InputRecipeUpdate!) {
    updateRecipe(id: $recipeId, recipe: $recipe) {
      ...GMenuRecipe
    }
  }
  ${MenuRecipeFragment}
`

export const MenuRecipeDeleteMutation = gql`
  mutation GMenuRecipeDelete($recipeId: ID!) {
    destroyRecipe(id: $recipeId) {
      id
    }
  }
`
