import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GOrderDriverSelection } from '../../../graph/types'

const DriverFragment = gql`
  fragment GDriver on User {
    id
    firstName
    lastName
  }
`

export const OrderDriverFragment = gql`
  fragment GOrderDriver on Order {
    id
    driver: userDeliveringOrder {
      id
      user {
        ...GDriver
      }
    }
  }
  ${DriverFragment}
`

const OrderDriverSelection = gql`
  query GOrderDriverSelection($orderId: ID!, $branchId: ID!) {
    order(id: $orderId) {
      ...GOrderDriver
    }
    drivers(companyBranch: $branchId, onlyAvailable: false) {
      ...GDriver
    }
  }
  ${OrderDriverFragment}
`

export const QueryDriverSelection = buildQuery<
  GOrderDriverSelection.Query,
  GOrderDriverSelection.Variables
>(OrderDriverSelection)
