import React from 'react'
import Spinner from 'react-spinkit'

import { Colors } from '../styling'

export interface IProps {
  noMargin?: boolean
}

const Pacman: React.SFC<IProps> = ({ noMargin = false }) => (
  <div
    style={{
      display: 'flex',
      alignSelf: 'center',
      justifyContent: 'center',
    }}
  >
    <Spinner
      name="pacman"
      style={noMargin ? {} : { marginTop: '45vh', color: Colors.orange }}
    />
  </div>
)

export default Pacman
