import React from 'react'

import { TRootModel } from '../models/RootModel'
import { WithRoot as RealWithRoot } from './RootProvider'

interface IProps {
  render(root: TRootModel): ReactNode
  observe?: boolean
}

export const WithRoot: React.SFC<IProps> = ({ render, observe }) => (
  <RealWithRoot children={render} observe={observe} />
)
