import React from 'react'
import Sound from 'react-sound'

import { logMain } from '../helpers/logger'
import { IntervalRender } from './IntervalRender'
import { WithRoot } from './WithRoot'

interface IProps {
  intervalSec: number
  soundUrl: string
  shouldPlay?: boolean
  skipEnableChecks?: boolean
}

export const SoundPlayInterval: React.SFC<IProps> = ({
  intervalSec,
  soundUrl,
  shouldPlay = true,
  skipEnableChecks = false,
}) => (
  <WithRoot
    observe={true}
    render={({ config, user }) => {
      if (!skipEnableChecks) {
        if (!config.soundEnabled) {
          logMain('sound is disabled')
          return null
        }
        if (!user.soundEnabled) {
          logMain('sound not enabled by user')
          return null
        }
      }
      logMain('sound play status: %s', shouldPlay)
      return (
        <IntervalRender
          intervalSec={intervalSec}
          enabled={shouldPlay}
          render={({ isActive, nextLoop }) => (
            <Sound
              url={soundUrl}
              playStatus={isActive ? 'PLAYING' : 'STOPPED'}
              onFinishedPlaying={nextLoop}
              loop={false}
              autoLoad={true}
              volume={user.soundVolume}
            />
          )}
        />
      )
    }}
  />
)
