import gql from 'graphql-tag'

import { CompanyBranchFragment } from '../graph/fragments'

export const UserModelBootstrapQuery = gql`
  query GUserModelBootstrap($userId: ID!) {
    user(id: $userId) {
      id
      language {
        rawCode
      }
      branches: accessibleBranches {
        ...GCompanyBranch
      }
    }
    permissions: __schema {
      queries: queryType {
        fields {
          name
        }
      }
      mutations: mutationType {
        fields {
          name
        }
      }
      subscriptions: subscriptionType {
        fields {
          name
        }
      }
    }
  }

  ${CompanyBranchFragment}
`
