import { addSeconds, differenceInMilliseconds } from 'date-fns'
import { Component } from 'react'

interface IRenderProps {
  isActive: boolean
  nextLoop(): void
}

interface IProps {
  intervalSec: number
  enabled?: boolean
  render(props: IRenderProps): ReactNode
}

interface IState {
  isActive: boolean
  nextTime: Date
}

export class IntervalRender extends Component<IProps, IState> {
  state: IState = { isActive: false, nextTime: this.getNextTime() }
  timer = 0
  componentDidMount() {
    this.executeTimer()
  }
  componentDidUpdate() {
    this.executeTimer()
  }
  componentWillUnmount() {
    if (this.timer) {
      clearTimeout(this.timer)
    }
  }
  getNextTime() {
    return addSeconds(new Date(), this.props.intervalSec)
  }
  executeTimer = () => {
    if (this.timer) {
      clearTimeout(this.timer)
      this.timer = 0
    }
    if (this.props.enabled === false) {
      return
    }
    const timeMs = differenceInMilliseconds(this.state.nextTime, new Date())
    // @ts-ignore (setTimeout returning NodeJS.Timer) for some weird reason
    this.timer = setTimeout(this.activate, timeMs)
  }
  activate = () => {
    if (this.props.enabled !== false) {
      this.setState({
        isActive: true,
        nextTime: this.getNextTime(),
      })
    }
  }
  deactivate = () => {
    this.setState({ isActive: false })
    this.executeTimer()
  }
  render() {
    const { isActive } = this.state
    return this.props.render({
      isActive,
      nextLoop: this.deactivate,
    })
  }
}
