import React from 'react'
import { Route, Switch } from 'react-router'

import Pacman from '../atoms/Pacman'
import { ErrorPage } from '../pages/ErrorPage'
import { NotFoundPage } from '../pages/NotFoundPage'
import { LazyRoute } from './LazyRoute'
import { LogRoute } from './LogRoute'
import { RedirectAfterLogin } from './RedirectAfterLogin'
import { RedirectAfterLogout } from './RedirectAfterLogout'
import { SecureRoute } from './SecureRoute'

const PasswordChangeRoute = React.lazy(() =>
  import('../segments/login/routes/PasswordChangeRoute'),
)

export const Routing = () => (
  <React.Suspense fallback={<Pacman />}>
    <LogRoute />
    <RedirectAfterLogin />
    <Switch>
      <LazyRoute
        path="/login"
        exact={true}
        component={React.lazy(() =>
          import('../segments/login/routes/PasswordLoginRoute'),
        )}
      />
      <LazyRoute
        path="/recovery/:token"
        exact={true}
        component={PasswordChangeRoute}
      />
      <LazyRoute
        path="/register/:token"
        exact={true}
        component={PasswordChangeRoute}
      />
      <SecureRoute
        path="/"
        exact={true}
        component={React.lazy(() =>
          import('../segments/main/pages/MainDashboardPage'),
        )}
      />
      <SecureRoute
        path="/order"
        component={React.lazy(() =>
          import('../segments/order/routes/OrderRoutes'),
        )}
      />
      <SecureRoute
        path="/settings"
        component={React.lazy(() =>
          import('../segments/settings/routes/SettingsRoutes'),
        )}
      />
      <SecureRoute
        path="/overview"
        component={React.lazy(() =>
          import('../segments/overview/routes/OverviewRoutes'),
        )}
      />
      <LazyRoute
        path="/support"
        component={React.lazy(() =>
          import('../segments/main/pages/SupportPage'),
        )}
      />
      <Route path="/error" component={ErrorPage} />
      <Route path="/logout" component={RedirectAfterLogout} />
      <Route component={NotFoundPage} />
    </Switch>
  </React.Suspense>
)
