import gql from 'graphql-tag'

export const ValidateLocationQuery = gql`
  query GValidateLocation(
    $branchId: ID!
    $latitude: Float!
    $longitude: Float!
  ) {
    resolveLocation(
      companyBranch: $branchId
      latitude: $latitude
      longitude: $longitude
    ) {
      inDeliveryLocation
    }
  }
`

export const CreateOrderMutation = gql`
  mutation GCreateOrder(
    $branchId: ID!
    $deliveryType: DeliveryTypeEnum!
    $gps: InputGeoPointCreate!
    $callEventId: ID
  ) {
    createOrder(
      order: {
        companyBranch: $branchId
        deliveryType: $deliveryType
        callEventId: $callEventId
        gps: $gps
        customer: {}
        warePriceType: DELIVERY
        orderOrigin: STAFF
      }
    ) {
      id
    }
  }
`

export const UpdateOrderMutation = gql`
  mutation GUpdateOrder(
    $orderId: ID!
    $deliveryType: DeliveryTypeEnum!
    $gps: InputGeoPointCreate!
  ) {
    updateOrder(
      id: $orderId
      order: { deliveryType: $deliveryType, gps: $gps }
    ) {
      id
    }
  }
`

export const AcceptOrderMutation = gql`
  mutation MAcceptOrder(
    $orderId: ID!
    $deliveryType: DeliveryTypeEnum!
    $customer: InputCustomerCreate!
    $gps: InputGeoPointCreate!
    $note: String
  ) {
    updateOrder(
      id: $orderId
      order: { deliveryType: $deliveryType, gps: $gps }
    ) {
      id
    }
    finishOrder(
      id: $orderId
      order: {
        customer: $customer
        note: $note
        paymentMethod: CASH
        paymentGate: CASH
      }
    ) {
      userErrors
    }
  }
`

export const CancelOrderMutation = gql`
  mutation MCancelOrder($orderId: ID!) {
    addOrderState(order: $orderId, orderState: CANCELED) {
      id
    }
  }
`
