import {
  GRemoveOrderDriver,
  GSelectOrderDriver,
  GSendOrderNotification,
  GUpdateOrderState,
  OrderStateEnum,
} from '../../../graph/types'
import { BaseModel } from '../../../models/BaseModel'
import {
  RemoveOrderDriverMutation,
  SelectOrderDriverMutation,
  SendOrderNotificationMutation,
  UpdateOrderStateMutation,
} from './OrderStateModel.graphql'

export const OrderStateModel = BaseModel.named('OrderState')
  .views(self => ({}))
  .actions(self => ({
    async updateOrderState(orderId: ID, state: OrderStateEnum) {
      const variables: GUpdateOrderState.Variables = {
        orderId,
        state,
      }
      await self.client.mutate({
        mutation: UpdateOrderStateMutation,
        variables,
      })
    },
  }))
  .actions(self => ({
    deliverOrder(orderId: ID) {
      return self.updateOrderState(orderId, OrderStateEnum.ON_WAY)
    },
    async sendTimeNotification(orderId: ID, minutes: number) {
      const variables: GSendOrderNotification.Variables = {
        orderId,
        minutes,
      }
      await self.client.mutate({
        mutation: SendOrderNotificationMutation,
        variables,
      })
    },
    async selectDriver(orderId: ID, driverId: ID) {
      const variables: GSelectOrderDriver.Variables = {
        orderId,
        driverId,
      }
      await self.client.mutate({
        mutation: SelectOrderDriverMutation,
        variables,
      })
    },
    async removeDriver(orderDriverId: ID) {
      const variables: GRemoveOrderDriver.Variables = {
        orderDriverId,
      }
      await self.client.mutate({
        mutation: RemoveOrderDriverMutation,
        variables,
      })
    },
  }))

export type TOrderStateModel = typeof OrderStateModel.Type
export type TOrderStateModelProps = typeof OrderStateModel.SnapshotType
