import gql from 'graphql-tag'

import { GeoPointFragment, PriceFragment } from '../../graph/fragments'

export const ZoneFragment = gql`
  fragment GZone on Zone {
    id
    name
    enabled
    priority
    companyBranch {
      id
      brand {
        id
      }
    }
    geoPolygon {
      ...GGeoPoint
    }
    priceRangeRules {
      ...GZonePriceRange
    }
    minimalOrderPrice {
      ...GPrice
    }
    zoneColor {
      id
      code
      color
    }
  }

  fragment GZonePriceRange on ZonePriceRangeRule {
    toPrice {
      ...GPrice
    }
    price {
      ...GPrice
    }
  }

  ${PriceFragment}
  ${GeoPointFragment}
`

export const ZoneLocationQuery = gql`
  query GZoneLocation($zoneId: ID!) {
    zone(id: $zoneId) {
      id
      companyBranch {
        id
        gps {
          ...GGeoPoint
        }
      }
    }
  }
  ${GeoPointFragment}
`

export const ZoneBranchLocationQuery = gql`
  query GZoneBranchLocation($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      gps {
        ...GGeoPoint
      }
    }
  }
  ${GeoPointFragment}
`

export const ZoneColorsQuery = gql`
  query GZoneColors {
    zoneColors {
      id
      code
      color
    }
  }
`
