import Color from 'color'
import React, { MouseEvent } from 'react'
import { Route } from 'react-router'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import { Icon } from '../atoms/Icon'
import { Colors, Radiuses, Spacing } from '../styling'

interface IHomeButtonProps {
  iconColor?: Colors
  color?: Colors
  disabled?: boolean
  onClick?(e: MouseEvent<HTMLButtonElement>): void
  goBack?: boolean
}

const applyAlpha = (alphaValue: number) => (props: IHomeButtonProps) =>
  Color(props.color)
    .alpha(alphaValue)
    .string()

const HomeButtonStyled = styled.button`
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0 0 ${Radiuses.Button} 0 !important;
  height: ${Spacing.ButtonHeight};
  width: ${Spacing.ButtonHeight};
  margin: 0;
  padding: 0.2rem;
  border: none;
  border-radius: ${Radiuses.Button};
  background-color: ${applyAlpha(0.7)};
  color: white;
  position: relative;
  font-size: 1rem;
  transition-duration: 0.2s;

  &:hover:enabled {
    background-color: ${applyAlpha(0.9)};
  }
  &:active:enabled {
    background-color: ${applyAlpha(1)};
  }
  &:disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
`

export const HomeButton: React.SFC<IHomeButtonProps> = ({
  color = Colors.greyMiddle,
  iconColor = Colors.white,
  goBack = false,
  ...props
}) => {
  if (goBack) {
    return (
      <Route>
        {({ history }) => (
          <HomeButtonStyled
            color={color}
            onClick={() => history.goBack()}
            {...props}
          >
            <Icon iconName="chevron_left" color={iconColor} fontSize={2.2} />
          </HomeButtonStyled>
        )}
      </Route>
    )
  }
  return (
    <Link to="/">
      <HomeButtonStyled color={color} {...props}>
        <Icon iconName="home" color={iconColor} fontSize={2.2} />
      </HomeButtonStyled>
    </Link>
  )
}
