import debug from 'debug'
import { getEnv, getPath, getRoot, types } from 'mobx-state-tree'
import { VError } from 'verror'

import { IRootEnvironment } from '../atoms/RootProvider'
import { TRootModel } from './RootModel'

export const BaseModel = types.model().views(self => ({
  get log() {
    return debug(`model/${getPath(self)}`)
  },
  get client() {
    const client = getEnv<IRootEnvironment>(self).apolloClient
    if (!client) {
      throw new VError('Apollo client is missing from environment setup')
    }
    return client
  },
  get root() {
    return getRoot<TRootModel>(self)
  },
}))
