import 'rc-slider/assets/index.css'

import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../molecules/TouchButton'
import { Colors, Spacing } from '../styling'
import { WithRoot } from './WithRoot'

const SoundControlContainerStyled = styled.div`
  position: relative;
`

const SoundButtonStyled = styled(TouchButton)`
  height: ${Spacing.ButtonHeight};
  width: ${Spacing.ButtonHeight};
  position: absolute;
  right: 0;
  top: 0;
`

export class SoundControl extends React.Component {
  state = { playPreview: false }
  render() {
    return (
      <WithRoot
        observe={true}
        render={({ user }) => (
          <SoundControlContainerStyled>
            <SoundButtonStyled
              color={user.soundEnabled ? Colors.green : Colors.red}
              iconName={user.soundEnabled ? 'volume_up' : 'volume_off'}
              iconSize={2}
              onClick={() => {
                user.controlSound(!user.soundEnabled)
              }}
            />
          </SoundControlContainerStyled>
        )}
      />
    )
  }
}
